* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Segoe UI", Arial, sans-serif;
  line-height: 1.4;
  color: #444;
  background: #fff;
  height: 100vh;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
}

.inner {
  width: 100%;
  padding: 8rem 10px 4rem;
}

.form-container {
  width: 100%;
  margin-bottom: 20px;
  display: flex;
  border-radius: calc(0.5 * 100px);
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.38);
  justify-content: space-evenly;
  background-color: rgb(57, 58, 58);
}

.input-text {
  font-size: 1rem;
  font-weight: 400;
  width: 85%;
  padding-right: 5px;
  padding-left: 10px;
  border-radius: calc(0.5 * 100px);
}

.input-text::placeholder {
  color: #000;
}

.input-submit {
  background: transparent;
  color: #5b5b5b;
  text-transform: capitalize;
  cursor: pointer;
  font-weight: 600;
  margin-right: 10px;
}

.input-text,
.input-submit {
  height: 45px;
  outline: none;
  border: none;
}

.active {
  color: rgb(156, 247, 204);
  text-decoration: underline !important;
  text-transform: uppercase;
}

a {
  color: azure;
  font-weight: bold;
}

a:hover {
  color: rgb(156, 247, 204);
  text-decoration: underline !important;
}

.link-hover:hover {
  transform: translate(10px);
}

@media (min-width: 886px) {
  .btn {
    display: none;
  }

  nav {
    background-color: rgb(0, 82, 82);
  }

  nav ul {
    display: flex !important;
    column-gap: 10px;
  }
}
